<template>
  <v-app>
    <nav>
        <v-app-bar color="#323232" dark app class="line" flat>
            <v-toolbar-title style="color:#de0d0d" class="text-uppercase red--text">
                <span class="font-weight-light">Cyber</span>
                <span>Defenders</span>
            </v-toolbar-title>
            <v-row justify="center">
              <v-btn @click="logout_()" color="red">
                Log Out
              </v-btn>
            <v-dialog
              v-model="dialog"
              persistent
              max-width="500px"
            >
            <template v-slot:activator="{ on, attrs }">
              <v-btn color="red" dark small absolute bottom right fab
                v-bind="attrs"
                v-on="on">
                <v-icon>mdi-plus</v-icon>
              </v-btn>
            </template>
            <v-card >
              <v-card-title>
                <span class="headline">Launch <strong>Instance</strong></span>
              </v-card-title>
              <v-card-text>
                <v-container class="white--text">
                  <v-row>
                    <v-col
                      cols="12"
                    >
                      <v-text-field
                        v-model="instance_name"
                        :rules="rules"
                        counter="10"
                        
                        label="Name"
                        color="black"
                      ></v-text-field>
                    </v-col>
                    <v-col
                      cols="12"
                      >
                      <v-autocomplete
                        v-model="instance_image"
                        color="black"
                        :items="available_images"
                        label="Image"
                      ></v-autocomplete>
                    </v-col>
                  </v-row>
                </v-container>
              </v-card-text>
            <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn
                color="red"
                text
                @click="dialog = false"
            >
              Cancel
            </v-btn>
            <v-btn
                color="red"
                text
                @click="create_instance"
            >
              Launch
            </v-btn>
            </v-card-actions>
          </v-card>
        </v-dialog>
      </v-row>
    </v-app-bar>

    </nav>
    <v-main>
      <v-container app>
      
        <v-row class="text-center">
          <v-col cols="12">
            <div v-for="(err, index) in errors" :key="index">
              <v-alert
                dense
                text
                type="error"
                dark
                dismissible
                >
                {{err}}
              </v-alert>
            </div>
            <div v-for="(suc, index) in success" :key="index">
              <v-alert
                dense
                text
                type="success"
                dark
                dismissible
                >
                {{suc}}
              </v-alert>
            </div>
          </v-col>
          <v-col cols="12">
            <h3 class="text-left spacing">#Your Instances</h3>
            <div class="text-right">
              <v-icon>mdi-account</v-icon> {{username}}
            </div>

            
            <v-divider
              unset
            ></v-divider>
            <v-simple-table
            class="pt-4"
    fixed-header
    height="400px"
    width="200px"
  >
    <template v-slot:default>
      <thead>
        <tr>
          <th class="text-left">
            Name
          </th>
          <th class="text-left">
            Image
          </th>
          <th class="text-left">
            Status
          </th>
          <th class="text-left">
            Date
          </th>
          <th class="text-left">
            Action
          </th>
        </tr>
      </thead>
      <tbody>
        <tr
          v-for="item in instances"
          :key="item.name"
        >
          <td class="text-left">{{ item.name }}</td>
          <td class="text-left">{{ item.image }}</td>
          <td class="text-left">
            <v-icon color='green' v-if="item.status == 'SUCCESS'">mdi-check-circle</v-icon>
            <v-icon color='red' v-if="item.status == 'FAILURE'">mdi-alert-circle</v-icon>
            <v-icon color='orange' v-if="item.status == 'PENDING'">mdi-clock</v-icon>
          </td>
          <td class="text-left">{{ item.date | format_date}}</td>
          <td class="text-left">
            <v-icon color="red" @click="remove_instance(item.name)">
              mdi-trash-can
            </v-icon>
          </td>
        </tr>
      </tbody>
          <v-dialog
      v-model="creation_dialog"
      hide-overlay
      persistent
      width="300"
    >
      <v-card
        color="#323232"
        dark
      >
        <v-card-text>
          Creating instance <strong v-if='instance_name!=""'>{{instance_name}}</strong>
          <v-progress-linear
            indeterminate
            color="red"
            class="mb-0"
          ></v-progress-linear>
        </v-card-text>
      </v-card>
    </v-dialog>
    </template>
  </v-simple-table>
          </v-col>
        </v-row>
      </v-container>
    </v-main>
  </v-app>
</template>

<script>

import axios from 'axios';
import moment from "moment";

export default {
    name: 'Home',
    data: () => ({
      username: "",
      instance_name: "",
      instance_image: "",
      rules: [v => v.length <= 10 || 'Max 10 characters', v => !!v || 'Container name is required',],
      dialog: false,
      creation_dialog: false,
      available_images:[],
      errors: [],
      success: [],
      instances: [],
    }),
    created(){
        this.get_instances()
        this.get_username()
        setInterval(() => {
          this.get_new_status()
        }, 5000);
        this.get_images()
    },
    filter:{
      format_date(value){
        return moment(String(value)).format('MM/DD/YYYY hh:mm')
      }
    },
    methods: {
      get_new_status(){
        this.instances.forEach(_in => {
            axios.post('/instances/status', {
              task_id: _in.task_id
            }).then(response => {
              _in.status = response.data.status
              if (_in.status == "FAILURE"){
                let err_str = "failed to create instance "+this.instance_name
                if (!this.errors.includes(err_str)){
                  this.errors.push("failed to create instance "+this.instance_name)
                }
              }
            }).catch(error => {
              console.log(error)
            })
        });
      },
      create_instance(){
        if (this.instance_name === "" && this.instance_image === ""){
          this.dialog = true
        }
        else{
          this.dialog = false
          this.creation_dialog = true
          axios.post('/instances/create', {
            name: this.instance_name,
            image: this.instance_image
          }).then(response => {
            setTimeout(()=>{this.creation_dialog = false}, 1500)
            this.instances.push(response.data)
            this.success.push("instance "+this.instance_name+" created.")
          })
          .catch(error =>{
            console.log(error)
          })
        }
      },
      remove_instance(inst_name){
        axios.post('/instances/remove', {
            name: inst_name,
          }).then(() => {
            this.instances = this.instances.filter(ins => { return ins.name !== inst_name})
            let err_str = "instance "+this.instance_name+ " removed!"
            if (!this.errors.includes(err_str)){
                this.errors.push("instance "+this.instance_name+ " removed!")
            }
          })
          .catch(error =>{
            console.log(error)
        })
      },
      get_images(){
        axios.get("/instances/images").then(response => {
          this.available_images = response.data.images
        })
      },
      get_username(){
        axios.get('/accounts/info').then(response => {
          this.username = response.data.username
        })
      },
      get_instances(){
        axios.get('/instances').then(response => {
          response.data.instances.forEach(ins => {
            this.instances.push(ins)
          })
        })
      },
      logout_(){
        window.location = "/accounts/logout"
      }
    },
  }
</script>

<style scoped>
.spacing {
  letter-spacing: 2px;
}
</style>
